.nav-wrapper {
  height: 10vh;
  display: flex;
  align-items: center;
  background-color: #222222;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}
.nav-flex {
  display: flex;
  max-width: 80%;
  justify-content: space-between;

  margin: 0 auto;
  width: 100%;
  align-items: center;
}
.nav-left {
  font-family: "Koulen", cursive;
}
.nav-center {
  display: flex;
}
.nav-center > li {
  cursor: pointer;
}
.nav-flex,
.nav-center > li {
  font-size: 18px;
  line-height: 10px;
  padding: 15px 30px;

  color: white;
  margin: auto;
}
.nav-center > li > a {
  color: white;
  font-family: "Koulen", cursive;
}
.nav-right > button {
  padding: 10px 20px;
  font-size: 17px;
  border-radius: 4px;

  color: white;
  background-color: #68d372;
  width: -webkit-max-content;
  width: max-content;
  /* text-transform: none; */
  font-weight: 400;
  cursor: pointer;
}
.hamb {
  display: none;
}

@media only screen and (max-width: 1400px) {
  .nav-flex {
    max-width: 90%;
  }
}
@media only screen and (max-width: 1100px) {
  .nav-flex {
    max-width: 95%;
  }
  .nav-center > li {
    padding: 15px 15px;
  }
}

@media only screen and (max-width: 900px) {
  .nav-center {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 90px;
    right: 0px;
    width: 250px;
    height: 100vh;
    background-color: #3f3f3f;

    transition: 0.01s ease-in-out;
  }
  .hamb {
    display: block;
  }
  .nav-flex {
    max-width: 90%;
  }
  .active {
    right: -250px;
  }
  .nav-center > li {
    margin: 0px;
    padding: 15px 15px;
    margin-bottom: 20px;
  }
}

