.form {
  display: flex;
  flex-direction: column;
  /* border: 1px solid black; */
  margin: auto;
  width: 550px;
  background-color: white;
  /* padding: 50px; */
}
.form > input {
  background-color: white;
  border-bottom: 1px solid grey;
}
.form > textarea {
  border-bottom: 1px solid grey;
}
.form > * {
  margin-bottom: 10px;
  /* border: 1px solid black; */
}
.send_btn {
  border: 1px solid red;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  padding: 12px 44px;
}
.contact-content {
  display: flex;
  border: 1px solid black;
