  justify-content: center;
  margin: auto;
  grid-gap: 70px;
  gap: 70px;
  padding: 80px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  box-sizing: border-box;
}
.contact-img > img {
  width: 350px;
}
